.profile__avatar-wrapper {
  position: relative;
  border-radius: 50%;
  cursor: pointer;
}

.profile__avatar-wrapper:before {
  content: "";
  position: absolute;
  background-image: url(../../../images/edit-avatar-profile.svg);
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 100px;
  width: 100%;
  height: 100%;
  visibility: hidden;
  background-size: 26px 26px;
  background-color: rgba(0, 0, 0, 0);
  transition: 0.3s linear;
}

.profile__avatar-wrapper:hover:before {
  visibility: visible;
  background-color: rgba(0, 0, 0, 0.8);
}
